@use 'sass:color';

// Core variables
$version: "0.5.8";

// Core features
$rtl: false !default;

$font-heading: 'Fira Mono';
$font-alt-heading: 'Londrina Solid';
$font-body: 'Sora';
$font-body-weight: 400;
$font-body-style: normal;
$font-alt-body: 'Sora';
$font-alt-body-weight: 400;
$font-alt-body-style: normal;


// Core colors
$primary-color: #FF7575;
// $primary-color: #9900ff;
$primary-color-dark: darken($primary-color, 30%);

// TODO: extract the lightness from primary color to decide the best amount to use for lighten
// i.e.: 30% lighten of already light colors result in a white color..
$primary-color-light:  lighten($primary-color, 15%);

$primary-color-alpha: rgba($primary-color, 0.35);
$primary-color-alpha-dark: rgba($primary-color-dark, 0.35);

$secondary-color: #474F90;
$secondary-color-dark: darken($secondary-color, 30%);
$secondary-color-light: lighten($secondary-color, 30%);

// Gray colors
$dark-color: #212121;
$light-color: #eeeeee;
$gray-color: lighten($dark-color, 55%);
$gray-color-dark: darken($gray-color, 30%);
$gray-color-light: lighten($gray-color, 20%);

$border-radius: 3%;
$border-color: lighten($dark-color, 65%);
$border-color-dark: darken($border-color, 10%);
$border-color-light: lighten($border-color, 8%);
$bg-color: $light-color;
$bg-color-dark: darken($bg-color, 10%);
$bg-color-light: lighten($bg-color, 10%);

// Control colors
$success-color: #32b643 !default;
$warning-color: #ffb700 !default;
$error-color: #e85600 !default;

// Other colors
$code-color: #d73e48 !default;
$highlight-color: #ffe9b3 !default;
$body-bg: $bg-color-light !default;
$body-font-color: lighten($dark-color, 5%);
$link-color: $primary-color-alpha !default;
$link-color-dark: darken($link-color, 10%);
$link-color-light: lighten($link-color, 10%);

// Fonts
// Credit: https://www.smashingmagazine.com/2015/11/using-system-ui-fonts-practical-guide/
$base-font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto !default;
$mono-font-family: "SF Mono", "Segoe UI Mono", "Roboto Mono", Menlo, Courier, monospace !default;
$fallback-font-family: "Helvetica Neue", sans-serif !default;
$cjk-zh-hans-font-family: $base-font-family, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", $fallback-font-family !default;
$cjk-zh-hant-font-family: $base-font-family, "PingFang TC", "Hiragino Sans CNS", "Microsoft JhengHei", $fallback-font-family !default;
$cjk-jp-font-family: $base-font-family, "Hiragino Sans", "Hiragino Kaku Gothic Pro", "Yu Gothic", YuGothic, Meiryo, $fallback-font-family !default;
$cjk-ko-font-family: $base-font-family, "Malgun Gothic", $fallback-font-family !default;
$body-font-family: $base-font-family, $fallback-font-family !default;

// Unit sizes
$unit-o: .05rem !default;
$unit-h: .1rem !default;
$unit-1: .2rem !default;
$unit-2: .4rem !default;
$unit-3: .6rem !default;
$unit-4: .8rem !default;
$unit-5: 1rem !default;
$unit-6: 1.2rem !default;
$unit-7: 1.4rem !default;
$unit-8: 1.6rem !default;
$unit-9: 1.8rem !default;
$unit-10: 2rem !default;
$unit-12: 2.4rem !default;
$unit-16: 3.2rem !default;

// Font sizes
$html-font-size: 20px !default;
$html-line-height: 1.5 !default;
$font-size: .8rem !default;
$font-size-sm: .7rem !default;
$font-size-lg: .9rem !default;
$line-height: 1.2rem !default;

// Sizes
$layout-spacing: $unit-2 !default;
$layout-spacing-sm: $unit-1 !default;
$layout-spacing-lg: $unit-4 !default;
$border-radius: $unit-h !default;
$border-width: $unit-o !default;
$border-width-lg: $unit-h !default;
$control-size: $unit-9 !default;
$control-size-sm: $unit-7 !default;
$control-size-lg: $unit-10 !default;
$control-padding-x: $unit-2 !default;
$control-padding-x-sm: $unit-2 * .75 !default;
$control-padding-x-lg: $unit-2 * 1.5 !default;
$control-padding-y: calc(($control-size - $line-height) / 2) - $border-width !default;
$control-padding-y-sm: calc(($control-size-sm - $line-height) / 2) - $border-width !default;
$control-padding-y-lg: calc(($control-size-lg - $line-height) / 2) - $border-width !default;
$control-icon-size: .8rem !default;

$control-width-xs: 180px !default;
$control-width-sm: 320px !default;
$control-width-md: 640px !default;
$control-width-lg: 960px !default;
$control-width-xl: 1280px !default;

// Responsive breakpoints
$size-xs: 480px !default;
$size-sm: 600px !default;
$size-md: 840px !default;
$size-lg: 960px !default;
$size-xl: 1280px !default;
$size-2x: 1440px !default;

$responsive-breakpoint: $size-xs !default;

// Z-index
$zindex-0: 1 !default;
$zindex-1: 100 !default;
$zindex-2: 200 !default;
$zindex-3: 300 !default;
$zindex-4: 400 !default;

$header-height: 4rem;

